.modalbtn {
  background-color: #2c3369;
  border: none;
  color: rgb(182, 192, 196);
  padding: 5px 0;
  text-decoration: none;
  font-size: 16px;
  border-radius: 5px;
  cursor: pointer;
  width: 80px;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.modal_bg {
  position: absolute;
  z-index: 1;
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
  background-color: rgba(24, 64, 116, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal_content {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  text-align: center;
  background-color: rgb(113, 123, 182);
  color: rgb(18, 29, 92);
  width: 90%;
  height: 20%;
  border-radius: 20px;
  max-width: 420px;
}

.modal_title {
  font-size: 24px;
  font-weight: bold;
}

/* edit scores modal */
.update_score {
  display: flex;
  justify-content: space-evenly;
  width: 100%;
}

/* adding player,lobby modal */
#add {
  height: 25%;
  padding-top: 5%;
  padding-bottom: 5%;
}

.btn-wrapper {
  width: 75%;
  display: flex;
  justify-content: space-evenly;
}

/* signup modal */
#signup {
  height: 50%;
  text-align: left;
}

.field > div {
  padding: 5px;
}

form {
  height: 85%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

.error {
  visibility: hidden;
  color: rgb(163, 7, 7);
  height: 1em;
  font-size: 14px;
  display: flex;
  align-items: center;
  margin: 0 10px;
  text-align: center;
}

.modal_content input:focus {
  outline: none;
}

.modal_content input {
  background-color: #2c3369;
  color: rgb(182, 192, 196);
  border: none;
  border-radius: 5px;
  height: 5dvh;
  font-size: 16px;
  padding: 0px 10px;
  text-align: left;
  width: 280px;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

/* rules modal */
#rules {
  height: auto;
}

#rules > * {
  margin: 15px;
}

p {
  text-align: left;
}

ul {
  padding-left: 15px;
  padding-right: 15px;
  text-align: left;
  margin: 0;
}

/* scoreboard modal */
.modal_active_scores {
  background-color: rgb(113, 123, 182);
  width: 90%;
  height: 80%;
  border-radius: 20px;
  display: grid;
  grid-template-rows: repeat(10, 1fr);
  grid-auto-flow: column;
  grid-auto-columns: minmax(0, 1fr);
  place-items: center;
  grid-column-gap: 0.3em;
}

.calloutcontent {
  height: calc(68dvh / 11);
  width: 100%;
  background-color: rgb(79, 86, 99);
  color: rgb(175, 171, 171);
  border-radius: 10px;
  font-size: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

#scores {
  width: 91%;
  height: 85%;
  margin-top: 10vh;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
