#form-wrapper {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.login {
  height: 40%;
  width: 90%;
  border-radius: 20px;
  max-width: 420px;
  background-color: rgb(113, 123, 182);
}

.login input {
  background-color: #2c3369;
  color: rgb(182, 192, 196);
  border: none;
  border-radius: 5px;
  height: 5dvh;
  font-size: 16px;
  padding: 0px 10px;
  text-align: left;
  width: 280px;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
