.rounded {
  display: flex;
  justify-content: space-between;
  height: 5dvh;
  align-items: center;
  background-color: rgb(113, 123, 182);
  color: rgb(18, 29, 92);
  border-radius: 10px;
  padding-left: 10px;
  padding-right: 10px;
}
.roundHeader {
  font-size: 20px;
  font-weight: bold;
  /* width: 50%; */
}

.playerscore {
  width: 100%;
  height: 76dvh;
  display: grid;
  grid-template-rows: repeat(11, 1fr);
  grid-auto-flow: column;
  grid-auto-columns: minmax(0, 1fr);
  place-items: center;
  grid-column-gap: 0.3em;
  padding-top: 1dvh;
  padding-bottom: 1dvh;
}

.user {
  display: flex;
  height: calc(76dvh / 12);
  width: 100%;
  background-color: rgb(51, 47, 47);
  color: rgb(175, 171, 171);
  border-radius: 10px;
  font-size: 20px;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.usercontent {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
  width: 100%;
}

.score {
  padding-right: 10px;
}
.reset {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(199, 55, 33);
  color: rgb(211, 211, 211);
  height: 4dvh;
  width: 20%;
  border-radius: 20px;
  font-size: 2dvh;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
