.lobby-wrapper {
  color: rgb(193, 220, 231);
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: calc(76dvh / 11);
  background-color: rgb(51, 47, 47);
  border-radius: 10px;
  font-size: 20px;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

#lobbies {
  width: 100%;
  display: grid;
  grid-template-rows: repeat(10, 1fr);
  grid-auto-flow: column;
  grid-auto-columns: minmax(0, 1fr);
  height: 76dvh;
  place-items: center;
  grid-column-gap: 0.3em;
  padding-top: 1dvh;
  padding-bottom: 1dvh;
}

.lobby {
  width: calc(100% - 2.7em);
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-left: 10px;
}

.rounds {
  font-size: 12px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  color: rgb(155, 204, 223);
  width: 66px;
  padding-left: 5px;
}
.lobby-name {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  width: calc(100% - 66px);
}

.note {
  width: 75%;
  height: 5dvh;
  background-color: rgb(79, 86, 99);
  color: rgb(175, 171, 171);
  border-radius: 10px;
  font-size: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
}
